import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EVDealershipFranchiseProviderCrudComponent } from './EV-dealership-franchise-provider-crud/EV-dealership-franchise-provider-crud.component';
import { TABS_DEMO_ROUTES } from './EV-dealership-franchise-provider-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : EVDealershipFranchiseProviderCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class EVDealershipFranchiseProviderRoutingModule{}